.gpt3__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 1rem;
}
.gpt3__features-container__feature-title {
  flex: 1;
  flex-basis: 100px;
  max-width: 300px;
  margin-right: 2rem;
}
.gpt3__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04em;
  /* display: grid; */

  color: #ffffff;
}
/* .gpt3__features-container__feature-title h1::before {
  content: "";
  margin-bottom: 0.5rem;
  width: 30px;
  height: 3px;
  background: var(--color-subtext);
} */
.gpt3__features-container__feature-title div {
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
  margin-bottom: 0.25rem;
}
.gpt3__features-container__feature-text {
  flex: 2;
  flex-basis: 390px;
  max-width: 390px;
  display: flex;
}
.gpt3__features-container__feature-text p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;

  /* or 188% */

  color: var(--color-text);
}

@media screen and (max-width: 550px) {
  .gpt3__features-container__feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }
  .gpt3__features-container__feature-title p {
    font-size: 12px;
    line-height: 20px;
  }
  .gpt3__features-container__feature {
    margin: 1rem 0;
  }
}
